<template>
  <v-container grid-list-xs>
    <v-row no-gutters>
      <v-col cols="12" md="6" align="center">
        <v-card max-width="500" :elevation="10">
          <div class="text-center py-10 blue darken-1">
            <v-avatar color="blue">
              <span class="white--text overline">{{getUserAvatar}}</span>
            </v-avatar>
          </div>
          <v-list class="py-0">
            <template v-for="(dato, i) in headers">
              <v-list-item :key="i" class="caption">
                <v-row align="center">
                  <v-col cols="6" class="overline text-left">{{dato}}</v-col>
                  <v-col cols="6">
                    <v-text-field
                      v-model="userData[dato]"
                      dense
                      hide-details="auto"
                      :disabled="!editando || !editarCampo(dato)"
                      class="my-0 text-right"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-list-item>
              <v-divider :key="`${i}d2`" />
            </template>
          </v-list>
          <v-card-actions>
            <v-row>
              <v-col cols="12">
                <v-btn
                  block
                  color="primary"
                  dark
                  @click="btnEditar"
                  :outlined="editando"
                >{{editando?'Cancelar':'editar informacion'}}</v-btn>
              </v-col>
              <v-expand-transition>
                <div v-if="editando" style="width: 100%">
                  <v-col cols="12" class="pt-0">
                    <v-btn
                      block
                      color="primary"
                      dark
                      :disabled="!editando"
                      @click="guardarCambios"
                    >guardar</v-btn>
                  </v-col>
                </div>
              </v-expand-transition>
            </v-row>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-col cols="12" md="6" align="center">
        <v-card
          class="pa-2 mt-5"
          max-width="500"
          color="blue-grey lighten-4"
          outlined
          :elevation="10"
        >
          <v-subheader class="overline">Otros ajustes</v-subheader>
          <v-card outlined class="mx-auto mt-5">
            <v-subheader class="overline">Cambiar contraseña</v-subheader>
            <v-form @submit.prevent="cambiar" ref="formPass">
              <v-card-text class="py-0">
                <v-list class="pb-0">
                  <v-text-field
                    label="Contraseña"
                    outlined
                    dense
                    class="caption"
                    type="password"
                    v-model="pass"
                    :rules="[rules.password]"
                  />
                  <v-text-field
                    label="Confirmar contraseña"
                    outlined
                    dense
                    :disabled="!pass"
                    class="caption"
                    type="password"
                    v-model="pass_confirm"
                    :rules="[rules.password]"
                  />
                  <v-expand-transition>
                    <div v-if="alert.show">
                      <v-alert type="warning" color="red" text>{{alert.msg}}</v-alert>
                    </div>
                  </v-expand-transition>
                </v-list>
              </v-card-text>
              <v-card-actions>
                <v-btn
                  color="primary"
                  block
                  :disabled="!pass || !pass_confirm"
                  @click="cambiar"
                >Cambiar contraseña</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="dialog.show" max-width="380" persistent >
      <v-card>
        <v-card-title class="headline">Confirmar cambios ({{dialog.father}})</v-card-title>
        <v-card-text pb-0>
          <v-row>
            <v-col cols="12">Debes de confirmar los cambios introduciendo tu contraseña actual</v-col>
            <v-col cols="12">
              <v-text-field
                label="Contraseña"
                outlined
                dense
                hide-details="auto"
                class="caption"
                type="password"
                :error="errord"
                v-model="dialog.pass"
              />
            </v-col>

            <v-col
              cols="12"
              v-if="dialog.father == 'PASS'"
              class="overline py-0"
            >NOTA: Al terminar se cerrara la sesion</v-col>
          </v-row>
        </v-card-text>
        <v-card-actions class="pt-0">
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="closeDialog">cancelar</v-btn>
          <v-btn color="blue darken-1" text @click="verifyPassword">aceptar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({
    editando: false,
    pass: null,
    pass_confirm: null,
    rules: {
      password: v =>
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[$@$!%*?&])([A-Za-z\d$@$!%*?&]|[^ ]){8,}$/.test(
          v
        ) ||
        "Minimo 8 caracteres, al menos una mayuscula, una minuscula, un numero y un caracter especial"
    },
    alert: {
      msg: "",
      show: false
    },
    userData: {
      nombre: "",
      email: "",
      telefono: "",
      direccion: ""
    },
    dialog: {
      father: "",
      show: false,
      pass: null,
      verified: false
    },
    errord: false
  }),
  computed: {
    getUserAvatar() {
      let name = this.usuario.nombre.split(" ");
      return name[0][0] + (name[1] ? name[1][0] : "");
    },
    headers() {
      return ["nombre", "email", "telefono", "direccion"];
    },
    ...mapState("auth", ["usuario"])
  },
  created() {
    this.$store.commit("navigate", false);
    this.userData = JSON.parse(JSON.stringify(this.usuario));
  },
  methods: {
    cambiar() {
    //   console.log(this.pass, this.pass_confirm);
      if (this.pass != this.pass_confirm) {
        this.alert.msg = "Las contraseñas no coinciden";
        this.alert.show = true;
        setTimeout(() => {
          this.alert.show = false;
        }, 2500);
      } else {
        this.openDialog("PASS");
      }
    },
    btnEditar() {
      if (this.editando) {
        this.userData = JSON.parse(JSON.stringify(this.usuario));
      }
      this.editando = !this.editando;
    },
    guardarCambios() {
      //funcion para guardar cambios de los datos del usuario
      this.openDialog("USER");
    },
    openDialog(father) {
      this.dialog = {
        father,
        show: true,
        pass: null
      };
    },
    async verifyPassword() {
      //funcion que sirve de pivote entre los datos del usuario y la contraseña
      const req = await fetch("https://radiant-oasis-39313.herokuapp.com/api/auth/verify", {
        method: "POST",
        body: JSON.stringify({
          email: this.usuario.email,
          password: this.dialog.pass
        }),
        headers: {
          "Content-Type": "application/json"
        }
      });
      const res = await req.json();
      if (res.status === "OK") {
        if (this.dialog.father === "USER") {
          await this.$store.dispatch("auth/actualizarInformacion", {
            userToChange: this.usuario.email,
            data: this.userData
          });
        }
        if (this.dialog.father === "PASS") {
          await this.$store.dispatch("auth/actualizarPass", {
            userToChange: this.usuario.email,
            data: this.pass
          });
          this.$refs.formPass.reset();
          setTimeout(() => {
            this.$store.commit("auth/logout");
            this.$router.push("/login");
          }, 1500);
        }
        this.dialog = {
          father: null,
          show: false,
          pass: null
        };
      }
      if (res.status === "AUTH_REJECTED") {
        this.errord = true;
        setTimeout(() => {
          this.errord = false;
        }, 2500);
      }
    },
    editarCampo(name) {
      if (name === "email") {
        return false;
      }
      return true;
    },
    closeDialog(){
        this.dialog.show = false
        this.$refs.formPass.reset()
    }
  }
};
</script>